
.homeLayout {
  grid-area: content;
  padding-right: 1000px;
  width: 100%;
  position: fixed;
  display: grid;
  grid-template-rows: 30vh 30vh 40vh;
  place-items: center;
  background: #111111;
  color: white;

  .homeHeader {
    grid-area: header;
    display: flex;
    justify-content: center;
    flex-direction: column;

    > .TitleContainer {
      transform: rotate(0.04deg);
      font-size: 64px;
      display: flex;
      flex-direction: row;

      > .homeHeaderTitleText {
        min-width: 427px;
      }

      > .homeHeaderTitleIcon {
        transform: rotate(0.04deg);
        font-size: 50px;
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;

      }

      > .homeHeaderTitleIcon:hover {
        transform: rotate(0.04deg);
        animation-duration: 0.5s;
        animation-name: shake;
        animation-iteration-count: infinite;
      }
    }

    > .content {
      font-size: 24px;
      font-weight: 200;
      font-family: "roboto", serif;
    }
  }
}

@media (min-width: 1080px) {
  .homeLayout {
    grid-template-columns: 200px repeat(12, 1fr) 200px;
  }
  @media (min-width: 1320px) {
    .homeLayout {
      column-gap: 20px;
      grid-template-areas:
    ". "+". . . . . . . . . . . ."+" ."
    ". "+". header header header header header . . . . . ."+" ."
    ". "+". . . . . . . . . . . ."+" .";
    }
  }
  @media (max-width: 1319px) {
    .homeLayout {
      column-gap: 10px;
      grid-template-areas:
    ". . . . . . . . . . . ."
    ". header header header header header header header header header header ."
    ". . . . . . . . . . . .";
    }
  }
}
